import {
    FormControl,
} from "@material-ui/core";
import styled from "styled-components";
import {SelectWidget} from "../../../widget/slect_tokens/select";
import {InputWidget} from "../../../widget/base/input";
import {useForm} from "react-hook-form";
import {ButtonWidget} from "../../../widget/base/button";
import {ApproveButton} from "../../../widget/base/approve_button";
import {useWeb3} from "../../../provider/web3_provider";
import React, {useCallback, useEffect, useState} from "react";
import { useGetTokenMin, useRedeem} from "../../../server/sunder";
import {getBalanceNumber, getMaxNumber} from "../../../utils/formatBalance";
import {useSendModal} from "../../../widget/modal/send_modal";
import BigNumber from "bignumber.js";
import {TokenInterFace} from "../../../web3/token.config";
import {TitleTextWidget} from "../../../widget/base/title";
import {WidthStyle} from "../../../widget/style/width_style";


export const RightsWidget: React.FC = () => {
    const {register, errors, handleSubmit, setValue} = useForm();

    const {tokenMap, contrcts} = useWeb3();
    let spenderAddress = contrcts.sunderAddress;
    const [key, setKey] = useState(Object.keys(tokenMap)[0]);
    let tokenItem: TokenInterFace = tokenMap[key];

    const [amountMax, setAmountMax] = useState(0);

    //  token balance min
    const amount = useGetTokenMin(tokenItem.address);

    const headerToken = useCallback(async () => {
        setAmountMax(getBalanceNumber(amount));
    }, [tokenItem, amount]);

    // redeem 
    const [facethConvert] = useRedeem();

    const sendFn = useSendModal(facethConvert);

    // 
    const handleLogin = useCallback(async (data: any) => {
        let _number = getMaxNumber(data['amount'], new BigNumber(amount));
        await sendFn(tokenItem.address, _number);
    }, [tokenItem, tokenItem, amount]);

    useEffect(() => {
        headerToken();
    }, [amount])

    return (
        <RightStyle>
            <div className="wap">
                <TitleTextWidget text={"Redeem"} title={'Step by step process:'} arrText={[
                    `When you redeem tokens, the protocol will take your dTokens and eTokens and redeem them for the Native token. Every Native token is made up with one dToken and one eToken.`,
                    `Select the Vault you would like, which contains the dTokens and eTokens. At the moment, you may select Uniswap or Compound, with more governance tokens coming soon.`,
                    `Enter the amount of Native tokens you would like to redeem and the below will show you how many dTokens and eTokens will be needed to redeem.`,
                    `Click confirm to begin the redemption process.`
                ]}/>
                <div className="inner">
                    <p className={'stake'}>Redeem your Native token by combining dTokens and eTokens</p>
                    <p className={'charged'}>0.1% fee charged on redemption</p>

                    <div className={'input'}>
                        <FormControl id="demo-simple-select-label" fullWidth={true}>
                            <SelectWidget
                                listToken={tokenMap}
                                value={key}
                                setValue={setKey}
                            />
                        </FormControl>
                    </div>
                    <div className={'input'}>
                        <InputWidget errors={errors}
                                     inputRef={register({
                                         required: 'Please enter a number > 0',
                                         max: {value: amountMax, message: `amountMax ${amountMax}`}
                                     })}
                                     sunTitle={
                                         <div>
                                             <p>Collateral: <a href={tokenItem.netWork}>{tokenItem.nick}</a></p>
                                         </div>
                                     }
                                     label={'Amount of dTokens and eTokens to redeem'}
                                     name={'amount'}
                                     placeholder={'0.00'}
                                     setValue={(ev) => {
                                         setValue(ev, amountMax)
                                     }}
                        >
                            <div className={'balance'}>Balance: <span>{amountMax}</span></div>
                        </InputWidget>
                        <div className={'bottoms'}>
                            <p className={'left'}> You have {getBalanceNumber(amount)} {tokenItem.nick} dToken
                                and {getBalanceNumber(amount)} {tokenItem.nick} eToken</p>
                            {/*<p className={'left'}>You have {tokenItem.nick} dTokens and  0Uniswap eTokens, and will receive {tokenItem.nick} Native tokens </p>*/}


                        </div>
                    </div>
                    <ApproveButton contract={tokenItem.contract} contractAddress={tokenItem.address}
                                   spender={spenderAddress}>
                        <ButtonWidget text={'Confirm'} onClick={handleSubmit(handleLogin)}/>
                    </ApproveButton>
                </div>
            </div>


        </RightStyle>
    );
}


const RightStyle = styled.div`
  ${() => WidthStyle.MinInnerStyle};

  .wap {
    background-size: 100% 100%;
  }

  .inner {
    padding: 10px 20px 32px 20px;
    box-sizing: border-box;
  }

  .stake {
    font-size: 14px;
    font-weight: 400;
    color: #696190FF;

    display: flex;
    justify-content: space-between;
  }

  .charged {
    font-size: 12px;
    margin-top: 5px;
    font-weight: 400;
    color: ${({theme}) => theme.color.oranges[100]};
    margin-bottom: 20px;
  }

  .input {
    margin-top: 20px;
    //padding-right: 20px;
    box-sizing: border-box;


    .bottoms {
      display: flex;
      justify-content: space-between;
      font-size: 12px;
      font-weight: 400;
      margin-bottom: 40px;
      margin-top: 30px;

      .left {
        width: 400px;
        color: ${({theme}) => theme.color.oranges[100]};
      }

      a {
        color: #2F35E5;
      }
    }


  }

  .balance {
    color: rgb(211, 211, 211);
    width: 200px;
  }
`
